.styled-checkbox {
  position: absolute;
  z-index: -100;
  left: -1000%;
}
.styled-checkbox__label {
  cursor: pointer;
  display: block;
  width: 18.5px;
  height: 18.5px;
  border-radius: 5px;
  border: 2px solid #6539c1;
  margin-right: 12px;
  background: #ffffff url("../../../../public/images/checkbox.svg") no-repeat center;
}
.styled-checkbox:checked + .styled-checkbox__label {
  background-color: #6539c1;
}/*# sourceMappingURL=styled-checkbox.css.map */